ponents/shoplist/src/components/shoplist/registerAudit.vue"],"names":[],"mappings":";AAsRA;EACA,qBAAA;CACA;AACA;EACA,mBAAA;EACA,6BAAA;EACA,gBAAA;EACA,iBAAA;CACA;AAEA;EACA,cAAA;EACA,aAAA;EACA,WAAA;EACA,6BAAA;EAAA,8BAAA;MAAA,iBAAA;UAAA,aAAA;EACA,cAAA;EACA,2BAAA;EACA,oBAAA;;EAEA,oBAAA;CACA;AACA;EACA,qBAAA;EAAA,qBAAA;EAAA,cAAA;EACA,6BAAA;EAAA,8BAAA;MAAA,iBAAA;UAAA,aAAA;CAEA;AAEA;EACA,aAAA;EACA,aAAA;EACA,oBAAA;EACA,uDAAA;UAAA,+CAAA;EACA,aAAA;EACA,aAAA;CACA;AAEA;EACA,iCAAA;EACA,kBAAA;CACA;AACA;EACA,gBAAA;CACA;AACA;EACA,YAAA;EACA,kBAAA;EACA,sBAAA;EACA,eAAA;CACA;AAEA;EACA,aAAA;EACA,sBAAA;EACA,oBAAA;EAAA,oBAAA;EAAA,aAAA;EACA,kBAAA;CACA;AACA;EACA,eAAA;EACA,qBAAA;MAAA,aAAA;UAAA,SAAA;EACA,4BAAA;EACA,kBAAA;EACA,8BAAA;UAAA,sBAAA;CACA;AACA;EACA,YAAA;EACA,eAAA;CACA;AACA;EACA,kBAAA;CACA;AACA;EACA,gBAAA;EACA,YAAA;CACA;AACA;EACA,cAAA;CACA","file":"registerAudit.vue","sourcesContent":["<template>\n  <div>\n    <ctbHead>\n      <router-link :class=\"seCurId==item.id? 'cur':''\" v-for=\"item in authList\" :key=\"item.id\" :to=\"{path:item.action,query:{id:curId}}\" :v-key=\"item.id\">{{item.name}}\n      </router-link>\n    </ctbHead>\n    <div class=\"container\">\n      <div class=\"quote\">\n        <div class=\"quote-ele\"><i></i>注册审核</div>\n      </div>\n      <table class=\"table\">\n        <thead>\n        <tr>\n          <th>公司名称</th>\n          <th>负责人</th>\n          <th>联系方式</th>\n          <th>申请时间</th>\n          <th>操作</th>\n        </tr>\n        </thead>\n        <tbody>\n        <tr v-for=\"item in list\">\n          <td class=\"t-l\">{{item.company}}</td>\n          <td>{{item.leader}}</td>\n          <td>{{item.phone}}</td>\n          <td>{{item.create_time}}</td>\n          <td>\n            <a href=\"javascript:;\" @click=\"eachData=item,detailsDialogVisible=true,sid=item.id,adopt(item.id)\">查看</a>\n          </td>\n        </tr>\n        </tbody>\n      </table>\n      <el-dialog\n        title=\"详情\"\n        :visible.sync=\"detailsDialogVisible\"\n        center\n        @close=\"operatorId = ''\"\n      >\n        <div\n          class=\"mybox\"\n          v-loading=\"loading\"\n          element-loading-text=\"加载中\"\n          element-loading-spinner=\"el-icon-loading\"\n          element-loading-background=\"rgba(255, 255, 255, 0.9)\"\n          style=\"width: 100%\"\n        >\n          <div class=\"myboxLeft\">\n            <el-form label-position=\"right\" label-width=\"90px\">\n              <el-form-item label=\"营业执照:\">\n                <img :src=\"registerDetail.license\" />\n              </el-form-item>\n              <el-form-item label=\"维修厂图片:\">\n                <img :src=\"handleImage(registerDetail.photo)\" />\n              </el-form-item>\n            </el-form>\n          </div>\n          <div class=\"myboxRight\">\n            <el-form label-position=\"right\" label-width=\"90px\">\n              <el-form-item label=\"维修厂简介:\">\n                <p class=\"noWrap\">{{registerDetail.about}}</p>\n              </el-form-item>\n              <el-form-item label=\"维修厂主修:\">\n                <p>{{registerDetail.major}}</p>\n              </el-form-item>\n              <el-form-item label=\"开户行:\">\n                <p>{{registerDetail.bank}}</p>\n              </el-form-item>\n              <el-form-item label=\"开户分行:\">\n                <p>{{registerDetail.branch}}</p>\n              </el-form-item>\n              <el-form-item label=\"开户名:\">\n                <p>{{registerDetail.account_name}}</p>\n              </el-form-item>\n              <el-form-item label=\"银行卡号:\">\n                <p>{{registerDetail.account}}</p>\n              </el-form-item>\n              <el-form-item label=\"地址:\">\n                <p>{{registerDetail.province}}{{registerDetail.city}}{{registerDetail.county}}</p>\n              </el-form-item>\n              <el-form-item label=\"详细地址:\">\n                <p>{{registerDetail.address}}</p>\n              </el-form-item>\n              <el-form-item label=\"绑定运营商:\">\n                <el-select v-model=\"operatorId\" clearable placeholder=\"请选择运营商\">\n                  <el-option\n                    v-for=\"item in options\"\n                    :key=\"item.aid\"\n                    :label=\"item.leader\"\n                    :value=\"item.aid\">\n                  </el-option>\n                </el-select>\n              </el-form-item>\n            </el-form>\n          </div>\n        </div>\n        <div style=\"text-align:center;padding-top:10px;box-sizing:border-box;\">\n          <el-button type=\"primary\" @click=\"handlePass\">通过</el-button>\n        </div>\n      </el-dialog>\n\n      <!-- 分页 -->\n      <div class=\"page_center\" v-show=\"pageCount&&pageCount>1\">\n        <paging :page-count=\"pageCount\" :page=\"page\" @index=\"paging\"></paging>\n        <!--分页的组件-->\n      </div>\n    </div>\n\n  </div>\n</template>\n<script type=\"text/ecmascript-6\">\n  import page from '../common.js';\n  import Viewer from 'viewerjs';\n  import 'viewerjs/dist/viewer.css';\n  export default {\n    data() {\n      return {\n        list: [],\n        token: window.sessionStorage.getItem('bbytoken'),\n        //分页数据\n        page: 1, //显示的页码数\n        totalpage: 1, //总页数\n        index: 1, //点击的当前页，\n        endpage: 1, //当前一组页码的最大值\n        arr: [], //默认显示 1 2 3 4 5页\n        n: 1, //当前点击的页码的位置\n        pageCount: 1,\n        authList: [],\n        curId: 0,\n        seCurId: 0,\n        detailsDialogVisible:false,\n        eachData:{},\n        registerDetail:{},\n        operatorId:'',\n        options: [],\n        sid: '',\n        loading: true\n      }\n    },\n    methods: {\n      go: page.go,\n      first: page.first,\n      end: page.end,\n      prev: page.prev,\n      next: page.next,\n      showpage: page.showpage,\n      paging(e) {\n        this.page = e;\n        this.init()\n      },\n      init() {\n        this.$axios.post('admin/ShopAudit/shopAuditList', {\n          token: this.token,\n          page: this.page\n        })\n          .then(res => {\n          \n            if(res.data.code == 1) {\n             \n              this.list = res.data.data.list;\n              this.showpage(res.data.data);\n              this.pageCount = res.data.data.rows\n            }else{\n              this.list=[]\n              layer.msg(res.data.msg)\n            }\n          })\n          .catch(err => console.log(err))\n      },\n      async adopt(id) {\n        this.loading = true;\n        try{\n          const res = await this.$axios.post('admin/ShopAudit/getShopDetail',{token:this.token,sid:id});\n          const res1 = await this.$axios.post('admin/ShopAudit/agentList', {token: this.token});\n          if(res.data.code==1 && res1.data.code==1){\n            this.registerDetail=res.data.data;\n            this.options = res1.data.data;\n            this.loading = false\n          }else{\n            this.$message({\n              type: 'error',\n              message: res.data.msg\n            })\n          }\n        }catch(err){\n          throw(err)\n        }\n        //图片查看器\n        const ViewerDom = document.querySelector('.mybox');\n        const viewer = new Viewer(ViewerDom, {\n\n          })\n      },\n      handleImage(images) {\n        if(images == undefined) {\n          return false\n        }\n        return images[0]\n      },\n      async handlePass() {\n        try {\n          let flag = false,self=this\n          let res = await this.$axios.post('admin/ShopAudit/passShop', {token: this.token, aid: this.operatorId, sid: this.sid})\n          if(res.data.code == 2) {\n            flag = true;\n            this.$message({\n              type: \"success\",\n              message: res.data.msg,\n              duration: 2000\n            });\n            setTimeout(() => {\n              self.$router.push({\n                path: '/shoplist/examine',\n                query: {\n                  id: this.curId\n                }\n              });\n            },2000)\n\n          }else if(res.data.code == 0) {\n            flag = true;\n            this.$message({\n              type: 'error',\n              message: res.data.msg\n            })\n          }\n          if(!flag) {\n            this.$message({\n              type: 'success',\n              message: res.data.msg\n            })\n            this.init();\n            this.detailsDialogVisible = false;\n          }\n        }catch (err){\n\n        }\n\n      }\n    },\n    created() {\n      this.init();\n    },\n    mounted() {\n      var id = this.$route.query.id;\n      this.curId = id;\n      this.$axios.post('admin/Auth/erAuth', {\n        token: window.sessionStorage.getItem('bbytoken'),\n        id: id\n      })\n        .then(res => {\n          if(res.data.code == 1) {\n            var arr = res.data.data;\n            for(var i = 0; i < arr.length; i++) {\n              if(arr[i].son) {\n                if(arr[i].name == '注册审核') {\n                  this.seCurId = arr[i].id;\n                }\n              }\n            }\n            this.authList = arr;\n          } else {\n            this.$alert(res.data.msg, '提示', {\n              type: 'error'\n            });\n          }\n        })\n        .catch(err => {\n          alert(err);\n        })\n    },\n    watch: {\n      index() {\n        this.init();\n      }\n    }\n  }\n</script>\n<style scoped>\n  .container{\n    /*margin-top: 50px;*/\n  }\n  .laybox {\n    text-align: center;\n    padding: 50px 68px 50px 36px;\n    font-size: 16px;\n    overflow: hidden;\n  }\n\n  .laybox textarea {\n    outline: none;\n    resize: none;\n    width: 80%;\n    flex-flow: 1;\n    height: 250px;\n    border: 1px dashed #04BBFC;\n    margin-bottom: 60px;\n\n    border-radius: 15px;\n  }\n  .laybox>p{\n    display: flex;\n    flex-flow: 1;\n\n  }\n\n  div.bot button {\n    width: 110px;\n    height: 36px;\n    border-radius: 18px;\n    box-shadow: 4px 0px 6px rgba(177, 42, 19, 0.2);\n    border: none;\n    color: white;\n  }\n\n  button.reject {\n    background: rgba(237, 81, 55, 1);\n    margin-right: 30%;\n  }\n    .details{\n    padding: 10px 0;\n  }\n  .details>p>span:first-child{\n    width: 75px;\n    text-align: right;\n    display: inline-block;\n    font-size:16px;\n  }\n\n  .mybox {\n    height:400px;\n    border:solid 1px #eee;\n    display:flex;\n    border-radius:5px;\n  }\n  .mybox>div {\n    height:inherit;\n    flex:0.5;\n    border-right:solid 1px #eee;\n    padding:10px 20px;\n    box-sizing:border-box;\n  }\n  .myboxLeft img {\n    width:150px;\n    cursor:pointer;\n  }\n  .myboxRight .el-form-item{\n    margin-bottom:0px;\n  }\n  .myboxRight p {\n    font-size: 13px;\n    color: #aaa;\n  }\n  .myboxRight {\n    overflow:auto;\n  }\n</style>\n"],"sourceRoot":""}]);

